.mlp-entity-edit-link {
    // Override the WordPress font size for links within the metabox handler.
    // Use the save value and unit as the metabox title.
    font-size: 14px !important;
}

.mlp-translation-metabox {

    margin-bottom: 1em;

    .wp-tab-panel {
        background-color: #fdfdfd;
        border-color: #ccc;
        border-top: none;
        border-left-style: solid;
        border-right-style: solid;
        border-width: 1px;
        max-height: none;
        padding-left: 2em;
        box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.15);

        .currently-connected {
            padding: 8px 12px;
            display: inline-block;
            color: #23282d;
            background-color: white;
            border-left: 4px solid #46b450;
            box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
            margin: 0 0 12px;
        }
    }

    .wp-tab-panel-inner {
        border-top: 1px solid #eee;
        padding: 0 2em;
    }

    .nav-tab-wrapper {
        border: none;
    }

    .nav-tab {
        margin: 0;
        border-left-width: 0;
        border-right: 1px solid #ccc;
        border-bottom-width: 0;
        border-left-width: 0;

        @include hover-state {
            background-color: #fdfdfd;
        }

        &:first-child {
            border-left-width: 1px;
        }

        &.ui-state-active {
            background-color: #fdfdfd;
            &.ui-state-disabled {
                @include hover-state {
                    background-color: #fdfdfd;
                }

                a {
                    color: #555;

                    @include hover-state {
                        color: #555;
                    }
                }
            }
        }

        &.ui-state-disabled {
            cursor: not-allowed;

            a {
                cursor: not-allowed;
                color: #ccc;

                @include hover-state {
                    color: #ccc;
                }
            }

            @include hover-state {
                background-color: #e5e5e5;
            }
        }

        a {
            text-decoration: none;
            color: #555;
            box-shadow: none;
            padding-left: 0.25em;
            padding-right: 0.25em;

            &:active {
                box-shadow: none;
                text-decoration: none;
            }
        }
    }

    .search-results {

        max-height: 10em;
        overflow-y: scroll;
        display: block;
        padding-top: 0;

        table {

            td {
                padding: 8px 10px;

                &.selected {
                    font-weight: bold;
                    background-color: #fff8e5;
                }
            }
        }
    }
}

.mlp-taxonomy-box {

    td > ul {

        display: block;
        background-color: white;
        margin: 5px 0 0 0;
        padding: 10px 10px 10px 15px;
        box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.07);
    }

    ul.children {
        padding: 0.25em 0 0.15em 1.25em;
    }
}

.mlp-warning {
    margin: 15px 0;
    border-left: 4px solid #dd3d36;
    padding: 1px 12px;
    background: #fdfdfd;
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .1);

    p {
        margin: .5em 0;
        padding: 2px;
    }
}
