body.toplevel_page_multilingualpress {

    .nav-tab {
        margin-left: 0;
        border-left-width: 0;
        padding-left: 2em;
        padding-right: 2em;

        &:first-child {
            border-left-width: 1px;
        }

        &-active {
            border-bottom: 1px solid white;
            background-color: white;
        }
    }

    .wrap > h1:first-child {
        margin: .67em 0;
    }

    .mlp-settings-table {
        border: 1px solid #ccc;
        border-top: none !important;

        &__option-description {
            margin-top: .63em;
        }

        tbody.table-body-block {
            padding: 1em 2em;
            display: inline-block;
        }

        tr {
            &:hover {
                background: #fefefe;
            }
        }

        tr.inactive {
            td, th {
                color: #666;

                &:hover {
                    color: #222;
                }
            }
        }

        td, th {
            vertical-align: top;
            color: #222;
        }

        .mlp-block-label {
            display: block;
        }
    }

    .mlp-module-settings {
        padding: 2em 0.5em 1em;

        td, th {
            padding-bottom: 1.5em;
        }

        .mlp-setting-name {
            display: block;
            margin-bottom: .17em;
        }
    }

    .form-table {
        border-top: none !important;
        margin-top: 0;
    }

    /*
     * Settings Specific Tabs
     */

    .mlp-internal-cache-settings {
        background: #fff;
        border: 1px solid #ccc;
        border-top: none !important;
        padding: 2em .5em 1em;

        &-section {
            & + .mlp-internal-cache-settings-section {
                margin-top: 10px;
            }

            &__title {
                margin-left: 0.7em;
                margin-top: 0;
            }

            // Remove border from table here to be add it to the container
            .mlp-settings-table {
                border: none;
                box-shadow: none;

                td {
                    padding-bottom: 1.5em;
                }

                &--cache {
                    label {
                        span {
                            display: block;
                        }
                    }
                }
            }
        }
    }

    .mlp-license-settings {
        td {
            input[type="text"], input[type="email"] {
                width: 100%;
                max-width: 25em;
            }
        }

        .mlp-licence-api-key-status {
            font-weight: bold;

            &[data-status="active"] {
                color: $color-green;
            }

            &[data-status="inactive"] {
                color: $color-gray;
            }
        }
    }
}

body.sites_page_multilingualpress-site-settings {

    input[type="text"], select {
        width: 100%;
        max-width: 25em;
    }
}

body.network-admin .section-mlp-new-site-settings,
body.network-admin .section-mlp-site-settings,
body.network-admin .section-permalink-site-settings {

    input[type="text"], select {
        width: 100%;
        max-width: 25em;
    }

    .mlp-relationships-bulk-selection {
        margin: .63em 0;
    }
}
