@import "../settings/colors";

.notice {
    br + .button {
        margin-top: 0.9375em;
    }

    &-multilingualpress {
        border-left-color: $color-green;
        padding-top: 10px;
        padding-bottom: 15px;

        .button-primary {
            background: $color-green;
            border-color: lighten($color-green, 10%) darken($color-green, 10%) darken($color-green, 10%);
            box-shadow: 0 1px 0 darken($color-green, 20%);
            color: $color-white;
            text-decoration: none;
            text-shadow: 0 -1px 1px darken($color-green, 20%), 1px 0 1px darken($color-green, 20%), 0 1px 1px darken($color-green, 20%), -1px 0 1px darken($color-green, 20%);

            &:active,
            &:hover,
            &:focus {
                background: darken($color-green, 10%);
                border-color: $color-green darken($color-green, 20%) darken($color-green, 20%);
                color: $color-white;
                text-decoration: none;
                text-shadow: 0 -1px 1px darken($color-green, 30%), 1px 0 1px darken($color-green, 30%), 0 1px 1px darken($color-green, 30%), -1px 0 1px darken($color-green, 30%);
            }

            &:active,
            &:hover {
                box-shadow: 0 1px 0 darken($color-green, 30%);
            }

            &:focus {
                box-shadow: inset 0 1px 0 darken($color-green, 30%);
            }
        }
    }
}

/**
 * Site Duplication Attachment Copier Notice
 */
.mlp-attachment-schedule-notice {
    padding: 1em;

    p:first-of-type {
        margin-top: 0;
    }

    p:only-child {
        margin-top: 0;
        margin-bottom: 0;
    }

    .mlp-new-site-schedule-notice-progress {
        background: $color-gray;
        margin-bottom: 0.73em;
        margin-left: 0.1875em;
        padding: 0 .63em 0 0;
        width: 300px;

        &__bar {
            background: $color-wp-notice-info;
            height: 2px;
            margin: 0;
            padding: 0;
            width: 75%;
        }
    }
}

@media only screen and (min-width: 48em) {
    /**
     * Site Duplication Attachment Copier Notice
     */
    .mlp-attachment-schedule-notice {
        align-items: center;
        display: flex;
        justify-content: space-between;
    }
}
