@mixin clearfix($extend: true) {
    @if $extend {
        @extend %clearfix;
    } @else {
        &:after {
            content: '';
            display: table;
            clear: both;
        }
    }
}

%clearfix {
    @include clearfix($extend: false);
}

@mixin hover-state {
    &:hover,
    &:active,
    &:focus,
    &.active {
        @content;
    }
}

@function image-url($file) {

    @return url("../images/#{$file}");
}
