.mlp-translation-metabox.ui-widget {
    font-family: -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen-Sans,
    Ubuntu,
    Cantarell,
    "Helvetica Neue",
    sans-serif;

    &-header {
        background: none;
    }

    &-content {
        border-color: transparent;
    }

    .ui-tabs-nav {
        background: none;
        padding: 0;

        li {
            margin: 0;
        }

        .ui-tabs-anchor,
        .ui-tabs-anchor:hover {
            color: #555;
        }

        .ui-tabs-anchor {
            text-decoration: none;
            box-shadow: none;
            padding: 5px 13px !important;
            display: block;
        }

        li.ui-tabs-active {
            padding-bottom: 5px;
        }
    }

    .nav-tab {
        font-size: 14px;
        line-height: 1.71428571;
        font-weight: 600;
        white-space: nowrap;
        float: left;
        border: 1px solid #ccc;
        border-bottom: none;
        margin-left: 0.5em;
        padding: 0px !important;;
        background: #e5e5e5;
        text-decoration: none;

        & .ui-tabs-anchor:hover,
        + .nav-tab:hover {
            cursor: pointer !important;
        }

        &.ui-state-disabled,
        &.ui-state-disabled .ui-tabs-anchor,
        &.ui-state-disabled .ui-tabs-anchor:hover {
            cursor: not-allowed !important;
        }

        &.ui-state-active .ui-tabs-anchor {
            color: #555 !important;
        }

        &.ui-state-disabled .ui-tabs-anchor {
            color: #ccc;
        }

        &.ui-state-active,
        &.ui-state-hover {
            background-color: #fdfdfd !important;
        }

        & + .nav-tab {
            border-left: none;
        }
    }

    .ui-corner-all,
    .ui-corner-bottom,
    .ui-corner-right,
    .ui-corner-br {
        border-bottom-right-radius: 0;
    }

    .ui-corner-all,
    .ui-corner-bottom,
    .ui-corner-left,
    .ui-corner-bl {
        border-bottom-left-radius: 0;
    }

    .ui-tabs .ui-tabs-panel {
        padding: 0;
    }

    .ui-state-disabled,
    .ui-widget-content,
    .ui-state-disabled,
    .ui-widget-header,
    .ui-state-disabled {
        opacity: 1;
        filter: Alpha(Opacity=100);
    }

    .ui-corner-all,
    .ui-corner-bottom,
    .ui-corner-right,
    .ui-corner-br {
        border-bottom-right-radius: 0;
    }

    .ui-corner-all,
    .ui-corner-bottom,
    .ui-corner-left,
    .ui-corner-bl {
        border-bottom-left-radius: 0;
    }

    .ui-corner-all,
    .ui-corner-top,
    .ui-corner-right,
    .ui-corner-tr {
        border-top-right-radius: 0;
    }

    .ui-corner-all,
    .ui-corner-top,
    .ui-corner-left,
    .ui-corner-tl {
        border-top-left-radius: 0;
    }
}
